<template>
  <div class="todo-list">
    <todo-list-item
      v-for="todo in todos"
      :key="todo.id"
      :todo-item="todo"
      @change-state="todo.completed=$event.target.checked"
    ></todo-list-item>
  </div>
</template>

<script>
import TodoListItem from "./TodolistItem.vue";
export default {
  name: "TodoList",
  components: { TodoListItem },
  props: ["todos"],
};
</script>

<style>
.todo-list {
  /* 设置grid布局，方便设置每个item的间距 */
  display: grid;
  row-gap: 14px;
}
</style>