<template>
  <main>
    <div class="container">
      <h1>欢迎使用 Minge待办事项</h1>
      <todo-add :tid="todos.length" @add-todo="addTodo"></todo-add>
      <todo-filter
        :selected="filter"
        @change-filter="filter = $event"
      ></todo-filter>
      <todo-list :todos="filteredTodos"></todo-list>
    </div>
  </main>
</template>

<script>
import TodoAdd from "./components/TodoAdd.vue";
import TodoFilter from "./components/TodoFilter.vue";
import TodoList from "./components/Todolist.vue";
import useTodos from "@/components/composables/useTodos.js";
import useFilteredTodos from "@/components/composables/useFilteredTodos.js";

export default {
  name: "App",
  components: { TodoAdd, TodoFilter, TodoList },
  setup() {
    const { todos, addTodo } = useTodos();
    const { filter, filteredTodos } = useFilteredTodos(todos);
    return {
      todos,
      addTodo,
      filter,
      filteredTodos,
    };
  },
}

</script>
