<template>
  <div class="filters">
      <span v-for="filter in filters" :key="filter.value" 
      @click="$emit('change-filter',filter.value)"
      class="filter"
      :class="{active:selected===filter.value}"
      >
        {{filter.label}}
      </span>
     </div>
</template>

<script>
export default {
name:"TodoFilter",
props:["seleceted"],
setup(){
  // filters的数据是静态的，不需要ref或者reactive包装。
  const filters=[
    {label:"全部",value:"all"},
    {label:"已完成",value:"done"},
    {label:"未完成",value:"todo"},
  ];
  return {
    filters
  };
}
}
</script>

<style>
/* 设置过滤标签 */
.filters{
  display: flex;
  margin:24px 2px;
  color: #c0c2ce;
  font-size: 14px;
}

.filters .filter{
  margin-right:14px;
  transition:0.8s;
  cursor:pointer;
}

.filters .filter.active{
  color: #6b729c;
  transform: scale(1.2);
}
</style>