<template>
  <div class="todo-item" :class="{done:todoItem.completed}">
    <label>
      <input type="checkbox" :checked="todoItem.completed" 
      @click="$emit('change-state',$event)"/>
      {{ todoItem.content }}
      <span class="check-button"></span>
    </label>
  </div>
</template>

<script>
export default {
  name: "TodoListItem",
  props: ["todoItem"],
};
</script>

<style>
.todo-item {
  background: white;
  padding: 16px;
  border-radius: 8px;
  color: #626262;
}

.todo-item label {
  position: relative;
  display: flex;
  align-items: center;
}

.todo-item.done label{
  text-decoration: line-through;
  font-style: italic;
}

/* 下面设置自定义的复选框。未选中时为空心圆，选中时为实心圆。通过absolute放到选择框上面 */
.todo-item label span.check-button {
  position: absolute;
  top: 0;
}

/* 用伪元素设置自定义的选择框 ，before为空圆，after为实圆，都为圆的公共样式*/
.todo-item label span.check-button::before,
.todo-item label span.check-button::after {
  content: "";
  display: block;
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;
}

/* 单独设置两个不同的圆 */
.todo-item label span.check-button::before {
  border: 1px solid #b382f9;
}

.todo-item label span.check-button::after {
  transition: 0.4s;
  background: #b382f9;
  /* 和空心圆边框同样的颜色，实心圆缩小为0.8倍，然后偏移1px,1px,就放置在空心圆的中间了 */
  transform: translate(1px, 1px) scale(0.8);
  /* 设置不透明度，选中是时为1 */
  opacity: 0;
}

.todo-item input {
  margin-right: 16px;
  /* 将原生的复选框设置为透明，隐藏起来。 */
  opacity: 0;
}

.todo-item input:checked + span.check-button::after {
  /* 当item选项被选中时，其紧跟着的after伪元素也显现出来 */
  opacity: 1;
}
</style>