<template>
  <div class="input-add">
    <input
      type="text"
      name="todo"
      v-model="todoContent"
      @keyup.enter="emitAddTodo"
    />
    <button @click="emitAddTodo">
      <i class="plus"></i>
    </button>
  </div>
</template>

<script>
import { ref } from "vue";
export default {
  name: "TodoAdd",
  setup(props, context) { 
    return useEmitAddTodo(props.tid,context.emit);
  },
};
//useEmitAddTodo函数内部代码较少，负责的功能只属于本组件内部。故不需要单独设置一个文件。
function useEmitAddTodo(tid,emit){
const todoContent = ref("");
    const emitAddTodo = () => {
      const todo = {
        id:tid,
        content: todoContent.value,
        completed: false,
       } ;
      emit("add-todo", todo);
      todoContent.value = "";
    };

    return {
      todoContent,
      emitAddTodo,
};
}
  
</script>

<style>
/* 添加框 */
.input-add {
  position: relative;
  display: flex;
  align-items: center;
}

.input-add input {
  /* 设置padding，使输入的文字与边框有间距，空白。撑开input盒子。 */
  padding: 16px 52px 16px 18px;
  border-radius: 48px;
  border: none;
  /* outline为浏览器内置的外边线，当选中的时候会呈现默认样式 */
  outline: none;
  box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.08);
  width: 100%;
  font-size: 16px;
  color: #626262;
}

.input-add button {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background: linear-gradient(#c0a5f3, #7f95f7);
  border: none;
  outline: none;

  /* 此处color设置的是button元素内的文字颜色 */
  color: white;
  /* 前面给父元素设置relative，此处设置absolute，将button固定放置在右边 */
  position: absolute;
  right: 0px;

  cursor: pointer;
}

/* 设置按钮 */
.input-add .plus {
  /* 为了使元素的宽和高有效，设为块级元素 */
  display: block;
  width: 100%;
  height: 100%;
  /* css的background-image可以设置多个。而linear-gradient渐变色返回的也是image。故也可设置多个 */
  background: linear-gradient(#fff, #fff), linear-gradient(#fff, #fff);
  background-size: 50% 2px, 2px 50%;
  background-position: center;
  background-repeat: no-repeat;
}
</style>